import React, { Component } from 'react';
import Card, { CardActions, CardContent, CardMedia } from 'material-ui/Card';
import Button from 'material-ui/Button';
import Dialog, { DialogTitle, DialogActions, DialogContent, DialogContentText } from 'material-ui/Dialog';
import TextField from 'material-ui/TextField';
import Snackbar from 'material-ui/Snackbar';
import Typography from 'material-ui/Typography';
import './Product.css'

function InvalidName(props) {
    if (props.invalidName) {
        return <h5>Invalid Name.</h5>;
    }
    else
        return false;
}

function InvalidNumber(props) {
    if (props.invalidNumber) {
        return <h5>Invalid Credit card Number.</h5>;
    }
    else
        return false;
}

function InvalidPassword(props) {
    if (props.invalidPassword) {
        return <h5>Incorrect CVV.</h5>;
    }
    else
        return false;
}



class Product extends Component {
    constructor() {
        super();
        this.handleChange = this.handleChange.bind(this);
        this.handleFormSubmitS = this.handleFormSubmitS.bind(this);
        this.state = {
            email: "",
            open: false,
            openS: false,
            items: [],
            vName: false,
            vNumber: false,
            vcvv: false,
        }
    }

    handleCart(res) {
        this.setState({ openS: true });
        fetch(`https://server-art-gallery.herokuapp.com/cart/add/${this.state.email}/${res.ref}`, {
            method: 'GET',
        })

    }
    handleClickOpen = () => {
        this.setState({ open: true });
    };
    handleCloseS = () => {
        this.setState({ openS: false });
    };
    handleClose = () => {
        this.setState({ open: false });
    };

    componentDidMount() {
        const Email = localStorage.getItem('email');
        this.setState({
            email: Email
        })
        fetch(`https://server-art-gallery.herokuapp.com/products`, {
            method: 'GET',
        })
            .then(res => res.json())
            .then(json => {
                this.setState({
                    items: json
                })
            });


    }

    render() {
        return (
            <div>
                <div className="wrapper">
                    {
                        this.state.items.map(res =>

                            <div key={res.ref} className="cardMargin">
                                <div className="cardWrap">
                                    <Card className="card">
                                        <CardMedia
                                            className="media"
                                            image={res.imgUrl}
                                            title={res.name}
                                        />
                                        <CardContent>
                                            <div className="action">
                                                <Typography variant="title" component="h2">
                                                    {res.name}
                                                </Typography>
                                                <Typography variant="subheading" component="h3">
                                                    {res.category}
                                                </Typography>
                                            </div>
                                            <div className="action">
                                                <Typography component="h3">
                                                    ₹{res.price}
                                                </Typography>
                                                <Typography component="h3">
                                                    {res.dimension}
                                                </Typography>
                                            </div>
                                        </CardContent>
                                        <CardActions className="action">
                                            <Button variant="raised" size="medium" color="primary" onClick={this.handleClickOpen.bind(this)} >
                                                Buy
                    </Button>
                                            <Button variant="raised" size="medium" color="secondary" onClick={this.handleCart.bind(this, res)}>
                                                Add To Cart
                    </Button>
                                        </CardActions>
                                    </Card>
                                </div>
                            </div>
                        )
                    }
                </div>
                <div>
                    <Dialog open={this.state.open} onClose={this.handleClose} >
                        <DialogTitle id="alert-dialog-title">{"Enter Shipping Address and Payment Details."}</DialogTitle>
                        <DialogContent>
                            <Typography variant="h6" gutterBottom>
                            </Typography>
                            <form onSubmit={this.handleFormSubmitS}>
                                <div container spacing={24}>
                                    <div item xs={12} md={6}>
                                        <TextField required id="cardName" onChange={this.handleChange} label="Name on card" name="nameS" fullWidth />
                                    </div>
                                    <div item xs={12} md={6}>
                                        <TextField required id="cardNumber" onChange={this.handleChange} type="number" label="Card number" name="contactnoS" fullWidth />
                                    </div>
                                    <div item xs={12} md={6}>
                                        <p>
                                            Expiry Date
                                        </p>
                                        <TextField required id="expDate" onChange={this.handleChange} type="date" label="" name="date" fullWidth />
                                    </div>
                                    <div item xs={12} md={6}>
                                        <TextField
                                            required
                                            type="number"
                                            name="passwordS"
                                            id="cvv"
                                            label="CVV"
                                            helperText="Last three digits on signature strip"
                                            fullWidth
                                            onChange={this.handleChange}
                                        />
                                    </div>
                                    <div item xs={12}>
                                    </div>
                                </div>
                                <DialogActions>
                                    <Button type='submit' onClick={this.handleFormSubmitS} color="primary" autoFocus>
                                        Proceed to checkout
                                    </Button>
                                </DialogActions>
                                <InvalidName invalidName={this.state.vName} />
                                <InvalidNumber invalidNumber={this.state.vNumber} />
                                <InvalidPassword invalidPassword={this.state.vPassword} />
                            </form>
                        </DialogContent>

                    </Dialog>
                    <Snackbar
                        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                        open={this.state.openS}
                        onClose={this.handleCloseS}
                        SnackbarContentProps={{
                            'aria-describedby': 'message-id',
                        }}
                        message={<span id="message-id">Added to Cart Sucessfully</span>}
                    />
                </div>
            </div>
        );
    }
    handleChange(e) {
        this.setState(
            {
                [e.target.name]: e.target.value,
                vName: false,
                vNumber: false,
                vPassword: false,
                userAlreadyExist: false
            }
        );
        console.log("hello");
    }

    handleFormSubmitS(e) {
        e.preventDefault();

        console.log(this.state);

        let vname = /^([a-zA-Z ]){2,30}$/;
        let vnum = /^([0-9]){16,16}$/;
        let vpass = /^([0-9]){3,3}$/;
        if (!vname.test(this.state.nameS)) {
            this.setState({
                vName: true,
            })
        }
        else if (!vnum.test(this.state.contactnoS)) {
            this.setState({
                vNumber: true,
            })
        }
        else if (!vpass.test(this.state.passwordS)) {
            this.setState({
                vPassword: true,
            })
        }
        else {
            this.setState({ open: false });
        }
    }


}


export default Product;



